/* eslint-disable indent */
import request from '@/utils/request'

/**
 * 
 * @param {*} params
 */
export function getCollegeItems(params = {}) {
    return request({
        url: '/college/items',
        method: 'get',
        params: params
    })
}

/**
 * 
 * @param {*} params
 */
export function getCollegeList(params = {}) {
    return request({
        url: '/college/list',
        method: 'get',
        params: params
    })
}

/**
 * 
 * @param {*} params
 */
export function createCollege(params = {}) {
    return request({
        url: '/college/create',
        method: 'post',
        data: params
    })
}

/**
 * 
 * @param {*} params
 */
export function updateCollege(id, params = {}) {
    return request({
        url: `/college/${id}/update`,
        method: 'post',
        data: params
    })
}

/**
 * 
 * @param {*} params
 */
export function deleteCollege(id, params = {}) {
    return request({
        url: `/college/${id}/delete`,
        method: 'post',
        data: params
    })
}
