import request from '@/utils/request'

/**
 * Dashboard
 * @param {*} params
 */
export function getDashboardInfo(params = {}) {
  return request({
    url: '/dashboard/info',
    method: 'get',
    params: params
  })
}

/**
 * Dashboard
 * @param {*} params
 */
export function getDashboardLine(params = {}) {
  return request({
    url: '/dashboard/line',
    method: 'get',
    params: params
  })
}
