/* eslint-disable indent */
import { invalid } from './Validator'
import store from '../../../store'

export const formData = {
    category: null,
    title: '',
    content: null,
    isEnable: true,
    photos: []
}

export const formItem = (options) => {
    let newOptions = JSON.parse(JSON.stringify(options))
    return {
        isEnable: { label: '', component: 'Switcher', required: false },
        category: {
            label: '',
            placeholder: '',
            state: true,
            invalid: invalid().invalidCategory,
            options: newOptions['category'],
            component: 'Select',
            required: true
        },
        title: {
            label: '',
            placeholder: '',
            state: true,
            invalid: invalid().invalidTitle,
            component: 'Input',
            required: true
        },
        content: {
            label: '',
            component: 'Textarea',
            state: false,
            required: false
        },
        photos: {
            label: '',
            state: false,
            component: 'UploadImages',
            action: store.getters.setting.domain + '/api/v1/admin/upload/image',
            required: false
        }
    }
}
