/* eslint-disable indent */
import request from '@/utils/request'

/**
 * 
 * @param {*} params
 */
export function getPageList(params = {}) {
    return request({
        url: '/page/list',
        method: 'get',
        params: params
    })
}

/**
 * 
 * @param {*} params
 */
export function createPage(params = {}) {
    return request({
        url: '/page/create',
        method: 'post',
        data: params
    })
}

/**
 * 
 * @param {*} params
 */
export function updatePage(id, params = {}) {
    return request({
        url: `/page/${id}/update`,
        method: 'post',
        data: params
    })
}

/**
 * 
 * @param {*} params
 */
export function deletePage(id, params = {}) {
    return request({
        url: `/page/${id}/delete`,
        method: 'post',
        data: params
    })
}
