/* eslint-disable space-before-function-paren */
/* eslint-disable indent */
import { invalid } from './Validator'
import store from '../../../store'

export const formData = {
    cover: [],
    title: '',
    onlineOffline: null, // 
    type: null, // 
    tabs: null,
    endAt: null,
    peopleLimit: 0,
    qualificationLimit: '',
    files: [],
    urls: ''
}

export const formItem = (options) => {
    let newOptions = JSON.parse(JSON.stringify(options))
    return {
        cover: {
            label: '',
            component: 'SingleUploadFile',
            state: false,
            required: false,
            accept: 'image/png, image/gif, image/jpeg',
            extensions: 'gif,jpg,jpeg,png',
            action: store.getters.setting.domain + '/api/v1/admin/upload/image?name=cover'
        },
        title: {
            label: '',
            placeholder: '',
            state: true,
            invalid: invalid().invalidTitle,
            component: 'Input',
            required: true
        },
        onlineOffline: {
            label: '',
            placeholder: '',
            options: newOptions['onlineOrOffline'],
            component: 'Select',
            required: true
        },
        type: {
            label: '',
            placeholder: '',
            options: newOptions['types'],
            component: 'Select',
            required: true
        },
        tabs: {
            label: '',
            placeholder: '',
            state: true,
            invalid: invalid().invalidTabs,
            options: newOptions['category'],
            component: 'Select',
            required: true
        },
        endAt: {
            label: '',
            component: 'DatePicker',
            invalid: invalid().invalidEndAt,
            state: true,
            required: true
        },
        peopleLimit: {
            label: '',
            component: 'Input',
            state: false,
            required: false,
            type: 'number'
        },
        qualificationLimit: {
            label: '',
            component: 'Textarea',
            state: false,
            required: false
        },
        files: {
            label: '',
            component: 'SingleUploadFile',
            state: false,
            required: false,
            accept: 'image/png, image/gif, image/jpeg, application/pdf, application/msword, application/vnd.openxmlformats-officedocument.wordprocessingml.document',
            extensions: 'gif,jpg,jpeg,png,pdf,doc,docx',
            action: store.getters.setting.domain + '/api/v1/admin/upload/image?name=files'
        },
        urls: {
            label: ',',
            placeholder: '',
            state: false,
            component: 'Input',
            required: false
        }
    }
}
